.basket {
  width: 300px;
  background-color: #FFFF;
  border-radius: 18px;
  margin-top: 72px;
}

.basket__header {
  display: flex;
  width: 268px;
  justify-content: space-between;
  margin: 0 auto;
  padding: 24px 0 14px;
  border-bottom: 1px solid #F2F2F3;
}

.basket__title {
  margin: 0;
  font-size: 24px;
  line-height: 24px;
  font-weight: 600;
}

.basket__number-orders {
  width: 41px;
  height: 20px;
  text-align: center;
  background-color: #F2F2F3;
  border-radius: 6px;
}

.basket__total {
  display: flex;
  width: 268px;
  margin: 0 auto;
  justify-content: space-between;
  padding: 15px 0 24px;
}

.basket__text {
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
}

.basket__total-price {
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
}

.basket__btn {
  width: 268px;
  height: 40px;
  text-align: center;
  background-color: #FF7020;
  border-radius: 12px;
  margin: 0 16px 8px;
  border: 0px;
  color: #F2F2F3;
  cursor: pointer;

}

.basket__btn:hover {
  background-color: #FFAB08;
}

.basket__svg {
  width: 24px;
  height: 24px;
  object-fit: cover;
  margin-right: 8px;

}

.basket__delivery {
  display: flex;
  align-items: center;
  padding-bottom: 24px;

  max-width: 268px;
}

.basket__container-delivery {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.basket__delivery-text {
  margin: 0;
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
}


.basket__btn_disabled {
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: transparent;
  color: rgba(0, 0, 0, 0.2);
  cursor: default;
}

.basket__btn_disabled:hover {
  background-color: transparent;
}


.basket__roll-up {
  color: #B1B1B1;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.basket__roll-up:hover {
  cursor: pointer;
  opacity: 0.6;
}



@media screen and (max-width:1000px) {
  .basket {
    margin: 0;
    margin-bottom: 30px;
    position: absolute;
    top: 0px;
  }

  .basket__header {
    cursor: pointer;
  }

  .basket__container {
    display: none;
  }

  .basket__container_visable {
    display: block;
  }
}